.textCrawlScene{padding:15rem 5rem;max-width:1000px;margin:0 auto}@media only screen and (max-width: 600px){.textCrawlScene{padding:1rem 1rem;margin-top:10px}}@media only screen and (min-width: 601px) and (max-width: 1500px){.textCrawlScene{padding:20vh 4rem;margin-top:0 auto}}.textCrawlActor{color:#fff;font-family:"Chivo",serif;font-size:1rem;line-height:1.4;font-size:2.8rem;display:flex;margin-bottom:5rem}.textCrawlActor:last-of-type{margin-bottom:15rem}@media only screen and (max-width: 600px){.textCrawlActor{font-size:2.5rem}}.actor__content{flex-grow:1}@-webkit-keyframes blink{from{opacity:0}to{opacity:1}}@keyframes blink{from{opacity:0}to{opacity:1}}.actor__content--typing::after{content:'|';-webkit-animation:blink 500ms infinite;animation:blink 500ms infinite}

