.pathDesktop{stroke-dasharray:1000;stroke-dashoffset:0;-webkit-animation:dash 2s linear alternate;animation:dash 2s linear alternate;display:block}@media only screen and (max-width: 600px){.pathDesktop{display:none}}.pathMobile{stroke-dasharray:1000;stroke-dashoffset:0;-webkit-animation:dash 2s linear alternate;animation:dash 2s linear alternate;display:none}@media only screen and (max-width: 600px){.pathMobile{display:block}}@-webkit-keyframes dash{from{stroke-dashoffset:1000}to{stroke-dashoffset:0}}@keyframes dash{from{stroke-dashoffset:1000}to{stroke-dashoffset:0}}

